<script>
export default {
  data() {
    return {
      filterInput: "",
      list: [
        { name: "Pogo Socket", linkpath: "/product/pogo_socket" },
        { name: "Rolling Socket", linkpath: "/product/rolling_socket" },
        { name: "POP Socket", linkpath: "/product/pop_socket" },
        { name: "Burn in Socket", linkpath: "/product/burn_in_socket" },
        { name: "PSR", linkpath: "/product/psr" },
        {
          name: "Pick & Place,SLT Model",
          linkpath: "/product/pick&place_slt_model"
        },
        { name: "Bowl Feeder Model", linkpath: "/product/bowl_feeder_model" },
        { name: "Pogo tower", linkpath: "/product/pogo_tower" },
        { name: "Parts", linkpath: "/product/parts" },
        { name: "Clean Unit", linkpath: "/product/clean_unit" },
        { name: "Laser Cleaner", linkpath: "/product/laser_cleaner" },
        { name: "靜電消散設備", linkpath: "/product/static_clean" },
        { name: "Socket Brush", linkpath: "/product/clean_brush" },
        { name: "抗沾黏coating", linkpath: "/product/anti_sticky_coating" },
        { name: "ESD coating", linkpath: "/product/esd_coating" }
      ]
    };
  },
  computed: {
    filterArray: function() {
      var vm = this;
      return vm.list.filter(function(item) {
        return item.name.match(vm.filterInput);
      });
    }
  }
};
